.wp-block-create-block-simplecal-calendar {
	color: #323232;
	padding: 4px;
}

.simplecal_event_meta {
	display: table;
	.simplecal_event_meta_row {
		display: table-row;
	}
	.simplecal_event_meta_label {
		display: table-cell;
		padding-right: 0.5em;
		text-transform: uppercase;
		&::after {
			content: ':';
		}
	}
	.simplecal_event_meta_value {
		display: table-cell;
	}
}

.simplecal_events_wrapper {
	min-height: 200px;
	max-height: 600px;
	overflow-y: scroll;
	scrollbar-color: #a0a0a0 rgba(0,0,0,0);
	scrollbar-gutter: stable;
}

.simplecal_no_events {
	font-style: italic;
	text-align: center;
	margin: 1em auto;
}

