.simplecal_theme1 {
	.simplecal_nav_pagination {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
	}
	.simplecal_nav_prev, .simplecal_nav_next {
		opacity: 0.5;
		display: flex;
		flex-flow: row nowrap;
		column-gap: 0.25em;
		justify-content: center;
		transition: 0.25s all ease-in-out;
		align-items: center;
		cursor: default;
		&.active {
			opacity: 1;
			cursor: pointer;

			&:hover {
				font-weight: bold;
			}
		}

		span { line-height: unset !important;}
	}
	
	.simplecal_list_year_header, .simplecal_list_month_header {
		margin: 0.25em 0;
		font-size: 1.5em;
		font-weight: bold;
	}
	.simplecal_list_month_header {
		font-size: 1.25em;
	}

	.simplecal_list_month_header ~ .simplecal_list_item_layout1 {
		margin-left: 2em;
	}
}

.simplecal_list_item_layout1 + .simplecal_list_item_layout1 {
	margin-top: 2em;
}

.simplecal_list_item_layout1 {
	display: flex;
	flex-flow: row nowrap;

	.simplecal_list_item_thumbnail {
		flex: 0 0 100px;
		margin-right: 10px;
		img {
			max-width: 100%;
		}
	}
	.simplecal_list_item_content_wrapper {
		flex: 1 1 auto;
		display: flex;
		flex-flow: column nowrap;;
		a {
			text-decoration: none;
			color: #1b4a70;
		}
		.simplecal_list_item_meta {
			display: flex;
			flex-flow: row nowrap;
			column-gap: 0.5em;
		}

		.simplecal_list_item_title {
			font-size: 1.25em;
			font-weight: bold;
		}
		.simplecal_list_item_datetime {
			color: #606060;
		}
		.simplecal_list_item_datetime {
			display: none;
		}

		.simplecal_list_item_excerpt {
			p:first-of-type {
				margin-top: 0;
			}
		}

		.material-symbols-outlined {
			vertical-align: middle;
			font-size: 1em;
		}
	}
}

.simplecal_past_event {
	opacity: 0.6;
}

.simplecal_list_item_layout2 {
	padding: 0.5em;
	margin: 1em;
	border: 1px solid #baba;
	border-radius: 8px;
	display: flex;
	flex-flow: row nowrap;
	column-gap: 0.25em;
}